.form {
    background-color: white;
    width: 500px;
    border-radius: 20px;
    margin: auto;
    box-shadow: 0 1.6px 3.6px 0 rgba(0,0,0,.132), 0 0.3px 0.9px 0 rgba(0,0,0,.108);
    padding: 40px;

    .header {
        font-size: 40px;

        .bold {
            font-weight: bold;
        }
    }

    .text {
        margin: 20px 0 20px 0;
    }

    .body {
        margin: 30px 0 30px 0;
    }

    button {
        float: right;
        margin-top: 10px;
        font-size: 20px;
        height: 45px;
        border-radius: 10px;
    }
}
